.gameFrameContainer {
  position: fixed;
  top: 50%;
  width: 100%;
}

.gameFrame {
  display: grid;
  position: relative;
  height: 756px;
  width: 100%;
  top: -378px;
  grid-template: 756px / 1fr 756px 1fr;
}

.nextRoundAnchor {
  position: fixed;
  z-index: 5;
  right: 50%;
  top: 50%;
}

.nextRoundAnnouncement {
  position: relative;
  left: 50%;
  bottom: 76px;
  text-align: center;
  height: 152px;
  width: 100%;
  z-index: 5;
  font-size: 64px;
  margin: 0px;
  color: white;
}

.snagQuestion {
  position: relative;
  left: 50%;
  top: 76px;
  font-size: 55px;
  outline-width: 2px;
  outline-style: solid;
  cursor: pointer;
}

.mainButtons {
  display: grid;
  min-width: 758px;
  grid-template:  repeat(2, 378px) / repeat(2, 378px);
  background-image: url(../../Images/StroopMainBackground.jpg);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.leftButtons {
  display: grid;
  position: relative;
  grid-template: repeat(2, 378px) / 1fr;
  min-width: 260px;
  background-position: left;
  background-image: url(../../Images/StroopLeftBackground2.jpg);
  background-repeat: no-repeat;
}

.rightButtons {
  display: grid;
  position: relative;
  right: 0%;
  grid-template:  repeat(2, 378px) / 1fr;
  min-width: 260px;
  background-position: right;
  background-image: url(../../Images/StroopRightBackground2.jpg);
  background-repeat: no-repeat;
}

.redButton {
  position: relative;
  width: 378px;
  height: 378px;
}

.redButtonMask {
  position: absolute;
  right: 0%;
  bottom: 0%;
  z-index: 3;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
}

.redRay {
  position: absolute;
  right: 0%;
  bottom: 0%;
  z-index: 2;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
}

.redBase {
  position: absolute;
  right: 0%;
  bottom: 0%;
  z-index: 1;
}

