.titleDisplay {
  display: grid;
  position: absolute;
  top: 0px;
  width: 100%;
  grid-template: 1fr / repeat(3, 1fr);
  text-align: center;
}

.topLeftSection {
  position: relative;
  top: 0px;
  min-width: 638px;
}

.leftTitleText {
  z-index: 4;
  color: rgb(175, 0, 0);
  position: absolute;
  left: 75px;
  top: -10px;
  font-size: 56px;
  font-family: myriad pro;
}

.roundResponding {
  z-index: 4;
  color: rgb(175, 0, 0);
  position: relative;
  width: 400px;
  left: 0px;
  top: -30px;
  line-height: 50px;
  font-size: 56px;
  font-family: myriad pro;
}

.topLeftFrame {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 3;
}

.topRightSection {
  position: relative;
  min-width: 638px;
}

.rightTitleText {
  z-index: 4;
  color: blue;
  position: absolute;
  right: 75px;
  top: -10px;
  font-size: 56px;
  font-family: myriad pro;
}

.scoreDisplay {
  z-index: 4;
  color: blue;
  width: 360px;
  position: absolute;
  right: 0px;
  top: -15px;
  line-height: 25px;
  font-size: 56px;
  font-family: myriad pro;
}

.score {
  z-index: 4;
  color: blue;
  position: absolute;
  width: 360px;
  text-align: center;
  right: 0px;
  top: 35px;
  line-height: 25px;
  font-size: 56px;
  font-family: myriad pro;
}

.topRightFrame {
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 3;
}



