.footerDisplay {
  display: grid;
  position: absolute;
  width: 100%;
  bottom: 0px;
  grid-template: 1fr / repeat(3, 1fr);
  text-align: center;
}

.bottomLeftSection {
  position: relative;
  bottom: 0px;
  color: red;
  min-width: 638px;
}

.bottomLeftText {
  position: absolute;
  z-index: 4;
  color: yellow;
  margin: 0px;
  padding: 0px;
  bottom: 0px;
  left: 75px;
  font-size: 50px;
  font-family: myriad pro;
}

.bottomLeftFrame {
  position: absolute;
  bottom: 0px;
  left: 0px;
  z-index: 3;
}

.bottomRightSection {
  position: relative;
  bottom: 0px;
  color: blue;
  min-width: 638px;
}

.bottomRightText {
  position: absolute;
  font-family: myriad pro;
  right: 50px;
  bottom: 25px;
  margin: 0px;
  font-size: 56px;
  z-index: 4;
  color: green;
}

.bottomRightFrame {
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 3;
}



