.snagInfoAnchor {
  display: grid;
  min-width: 758px;
  position: absolute;
  left: 50%;
  top: -50%;

  -webkit-transition: 0.2s;

  -o-transition: 0.2s;

  transition: 0.2s;
  z-index: 6;
}

.snagInfo {
  position: relative;
  top: -50%;
  left: -50%;
  z-index: 7;
}

.snagInfoBackground {
  position: relative;
  width: 758px;
  height: 758px;
  top: 50%;
  bottom: 50%;
  left: 0;
  z-index: 5;
}

.snagDescription {
  position: relative;
  grid-column-start: span 2;
  z-index: 8;
  font-size: 26px;
}

.snagGrid {
  display: grid;
  grid-template:  repeat(8, 1fr) / 2fr 5fr;
  position: relative;
  width: 675px;
  height: 675px;
  bottom: 50%;
  left: 5%;
  z-index: 8;
  color: white;
  text-align: center;
}

.snagBox {
  font-size: 26px;
  text-align: center;
  line-height: 300%;
  height: 30px;
}

.closeBox {
  grid-column-start: span 2;
  font-size: 56px;
  line-height: 175%;
  border-color: white;
  border-style: solid;
  border-width: 2px;
  cursor: pointer;
}



